import React from 'react';
import clsx from 'clsx';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import CssBaseline from '@material-ui/core/CssBaseline';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import List from '@material-ui/core/List';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import MailIcon from '@material-ui/icons/Mail';
import MessageIcon from '@material-ui/icons/Message';
import Container from '@material-ui/core/Container';
import { Link } from "react-router-dom";
import Card from "./Card";

import "./Style.css";

const drawerWidth = 240;

const useStyles = makeStyles(theme => ({
  root: {
    display: 'flex',
  },
  appBar: {
      backgroundColor: "black",
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    width: `calc(100% - ${drawerWidth}px)`,
    marginLeft: drawerWidth,
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  hide: {
    display: 'none',
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
  },

  drawerPaper: {
    position: "fixed",
    whiteSpace: 'nowrap',
    width: drawerWidth,
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },

  drawerHeader: {
    display: 'flex',
    alignItems: 'center',
    padding: theme.spacing(0, 1),
    ...theme.mixins.toolbar,
    justifyContent: 'flex-end',
  },
  content: {
    flexGrow: 0,
    padding: theme.spacing(0),
    transition: theme.transitions.create('margin', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    marginLeft: -drawerWidth,
  },

 
  container: {
    paddingTop: theme.spacing(0),
    paddingLeft: theme.spacing(0),
    paddingBottom: theme.spacing(0),
  },


  contentShift: {
    transition: theme.transitions.create('margin', {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
    marginLeft: 0,
  },
}));

export default function PersistentDrawerLeft() {
  const classes = useStyles();
  const theme = useTheme();
  const [open, setOpen] = React.useState(false);

  const handleDrawerOpen = () => {
    setOpen(true);
  };

  const handleDrawerClose = () => {
    setOpen(false);
  };

  return (
    <div className={classes.root}>
      <CssBaseline />
      <AppBar
        position="fixed"
        className={clsx(classes.appBar, {
          [classes.appBarShift]: open,
        })}
      >
        <Toolbar>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            onClick={handleDrawerOpen}
            edge="start"
            className={clsx(classes.menuButton, open && classes.hide)}
          >
            <MenuIcon />
          </IconButton>
          <Typography variant="h6" noWrap>

              <div className="ui mini circular image">
                  <img src="zaid.png" />
              </div>

            <Link to="/">
                ZAID KHAN
            </Link>
          </Typography>


  
     

        </Toolbar>
      </AppBar>
      <Drawer
        className={classes.drawer}
        variant="persistent"
        anchor="left"
        open={open}
        classes={{
          paper: classes.drawerPaper,
        }}
      >
        <div className={classes.drawerHeader}>
          <IconButton onClick={handleDrawerClose}>
            {theme.direction === 'ltr' ? <ChevronLeftIcon /> : <ChevronRightIcon />}
          </IconButton>
        </div>
        <Divider />
        <List>
       
            <ListItem button>
                <ListItemIcon>
                    <MessageIcon />
                </ListItemIcon>
                <Link to="/">
                <ListItemText primary="Dashborad" />
                </Link>
            </ListItem>

            <ListItem button>
                <ListItemIcon>
                    <MessageIcon />
                </ListItemIcon>
                <Link to="/portfolio">
                <ListItemText primary="Portfolio" />
                </Link>
            </ListItem>

            <ListItem button>
                <ListItemIcon>
                    <MessageIcon />
                </ListItemIcon>
                <Link to="/myoffer">
                <ListItemText primary="My Offer" />
                </Link>
            </ListItem>

            <ListItem button>
                <ListItemIcon>
                    <MessageIcon />
                </ListItemIcon>
                <Link to="/mycontact">
                <ListItemText primary="My Contact" />
                </Link>
            </ListItem>

        </List>
        <Divider />
        <List>
      
            <ListItem button>
                <ListItemIcon>
                    <MessageIcon />
                </ListItemIcon>
                <Link to="/mycv">
                <ListItemText primary="My CV" />
                </Link>
            </ListItem>

            <ListItem button>
                <ListItemIcon>
                    <MessageIcon />
                </ListItemIcon>
                <Link to="/mytechtalks">
                <ListItemText primary="My Techtalks" />
                </Link>
            </ListItem>

            <ListItem button>
                <ListItemIcon>
                    <MessageIcon />
                </ListItemIcon>
                <Link to="/myblog">
                <ListItemText primary="Personal Blog" />
                </Link>
            </ListItem>
            <br/>

            <a href="#"><i className="big mail black icon"></i></a>
            <a href="#"><i className="big facebook black icon"></i></a>
            <a href="#"><i className="big github black icon"></i></a>
            <a href="#"><i className="big twitter black icon"></i></a>
            <a href="#"><i className="big linkedin black icon"></i></a>
            <a href="#"><i className="big whatsapp black icon"></i></a>

        </List>
      </Drawer>
      <main className={classes.content}>
        <div className={classes.appBarSpacer} />
        <Container maxWidth="lg" className={classes.container}>

        <Card />


          
       
        </Container>
      </main>
    </div>
  );
}